import React, { Component } from 'react'
import './Tour.scss'

class Tour extends Component {
    state={
        showInfo:false
    }
    infoHandler=() =>{
        this.setState({
            showInfo:!this.state.showInfo
        })
    }
    
    render() {
        const {id,city,img,name,info,ref}=this.props.tour
        const {removeTour}=this.props
        return (
            <article className="tour">
                <div className="img-container">
                    <a href={ref} target="_blank" rel="noreferrer"><img  src={img} alt=""/></a>
                    <span className="close-btn" onClick={()=>{removeTour(id)}}>
                        <i className="fas fa-window-close" />
                    </span>
                </div>
                <div className="tour-info">
                    <h3>{city}</h3>
                    <h4>{name}</h4>
                    <h5>Info{" "} <span  onClick={this.infoHandler}><i className="fas fa-caret-square-down"></i></span></h5>
                    {this.state.showInfo && <p>{info}</p>}
                    
                </div>
            </article>
        )
    }
}

export default Tour